<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{ name: 'product', params: { id: product.id } }">
      <img :src="product.image" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#"> {{ product.title }} </a>
    </h3>

    <span class="catalog__price"> {{ product.price | numberFormat }} ₽</span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in colorsArr" :key="color.index">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" value="#73B6EA" />
          <span class="colors__value" :style="`background: ${color}`"> </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

import gotoPage from '@/helpers/gotoPage';

export default {
  data() {
    return {
      colorsData: null,
      color: '',
    };
  },
  filters: {
    numberFormat,
  },

  props: ['product'],
  computed: {
    colorsArr() {
      const colorArr = this.product.colors;
      return colorArr.map((e) => e.code);
    },
  },

  methods: {
    gotoPage,
  },
};
</script>
